import React, { Component, Fragment } from "react";
import gql from "graphql-tag";

import { Query } from "react-apollo";
import { Link } from "react-router-dom";

const LAUNCH_QUERY = gql`
  query LaunchQuery($flight_number: Int!) {
    launch(flight_number: $flight_number) {
      flight_number
      mission_name
      launch_year
      launch_success
      launch_date_local
      rocket {
        rocket_id
        rocket_name
        rocket_type
      }
    }
  }
`;
export class Launch extends Component {
  render() {
    let { flight_number } = this.props.match.params;
    flight_number = parseInt(flight_number);
    
   

    return (
      <Fragment>
        <Query query={LAUNCH_QUERY} variables={{ flight_number }}>
          {({ loading, error, data }) => {
            if (loading) return <h4>Loading...</h4>;
            if (error) console.log(error);

            const {
              mission_name,
              flight_number,
              launch_year,
              launch_success,
              rocket: { rocket_id, rocket_name, rocket_type }
            } = data.launch;

            return (
              <div>
                <h1 className="display-4 my-3">
                  <span className="text-dark">Mission:</span>
                  {mission_name}
                </h1>
                <h4 className="mb-4">Launch Details:</h4>
                <ul className="list-group">
                  <li className="list-group-item">
                    Flight Number: {flight_number}
                  </li>
                  <li className="list-group-item">
                    Launch Year: {launch_year}
                  </li>
                  <li
                    className="list-group-item"
                    style={{ listStyleType: "none" }}
                  >
                    Launch Success:{" "}
                    <span
                      className={
                        "text-" + (launch_success ? "success" : "danger")
                      }
                    >
                      {launch_success ? "Yes" : "No"}
                    </span>
                  </li>
                </ul>
                <h4 className="my-3">Rocket Details:</h4>
                <ul className="list-group">
                  <li className="list-group-item">Rocket Id: <Link to={`/rocket/${rocket_id}`}>{rocket_id}</Link></li>
                  <li className="list-group-item">
                    Rocket Name:
                    <span className="text-warning"> {rocket_name}</span>
                  </li>
                  <li className="list-group-item">
                    Rocket Type:{" "}
                    <span className="text-primary">{rocket_type}</span>
                  </li>
                </ul>
                <hr />
                <Link to="/" className="btn btn-secondary">
                  Back
                </Link>
              </div>
            );
          }}
        </Query>
      </Fragment>
    );
  }
}

export default Launch;
